@import "../../scss/scss-varibales";

.modal {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.8);
  overflow-y: auto;
  overflow-x: hidden;
  //padding-bottom: 50px;
  z-index: 3;
  height: 100vh;

  display: flex;
  //align-items: center;
  justify-content: center;
  opacity: 0;
  transition: all 0.3s ease-in-out;
  pointer-events: none;
  visibility: hidden;

  &__close-icon {
    font-size: 50px;
    color: #8C8C8C;
    position: fixed;
    top: 20px;
    right: 40px;
  }

  &.show {
    visibility: visible;
    opacity: 1;
    pointer-events: visible;

    & .modal-content {
      opacity: 1;
      //transform: translateY(0);
    }
  }
}
.modal-content {
  border-radius: 15px;
  //transform: translateY(-200px);
  transition: all 0.3s ease-in-out;
  margin: 100px 0;
  opacity: 0;
  background-color: $primary-bg-color;
  height: fit-content;
}

.modal-header,
.modal-footer {
  padding: 10px;
}

.modal-title {
  margin: 0;
}


.modal-body {
  padding: 20px;
  border-top: 1px solid #eee;
  border-bottom: 1px solid #eee;
  border-radius: 15px;
}